import React, { useEffect, useState } from "react";
import { Container } from "@mui/system";
import { useDispatch } from "react-redux";
import { useLocation } from "react-router";
import { getCities, getCurrencyList } from "../../redux/reducers/main";
import {
  getCategories,
  getProducts,
  getProductsByBrand,
  getProductsHotSellingCatalog,
  getProductsPromotionCatalog,
} from "../../redux/reducers/products";
import Form from "./Form";
import Products from "./Products";
import { Box } from "@mui/material";
import { motion } from "framer-motion";

export const MBox = motion(Box);

const Filter = ({
  t,
  brand,
  setLoading,
  search,
  setSearch,
  loading,
  setCartSum,
  cartSum,
}) => {
  const dispatch = useDispatch();
  const location = useLocation();

  const [sortingBy, setSortingBy] = useState(1);

  useEffect(() => {
    document.title = `Каталог Электроника сервис`;
    dispatch(getCategories());
    dispatch(getCurrencyList());
    // dispatch(getCities());

    if (location.search) {
      if (location.search === "?top_sellers")
        dispatch(getProductsHotSellingCatalog());
      else if (location.search === "?promotitons")
        dispatch(getProductsPromotionCatalog());
      else if (brand) dispatch(getProductsByBrand(location.search.split("=")[1]));
      else dispatch(getProducts(location.search));
    } else dispatch(getProducts());
    console.log(location.search);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <MBox
        component="section"
        initial="hidden"
        whileInView="visible"
        variants={{ hidden: { opacity: 0 }, visible: { opacity: 1 } }}
        transition={{ ease: "easeOut", duration: 0.1 }}
        pb={2}
      >
        <Container maxWidth="xl">
          {!brand && (
            <Form
              t={t}
              setSearch={setSearch}
              search={search}
              setLoading={setLoading}
              setSortingBy={setSortingBy}
              sortingBy={sortingBy}
            />
          )}

          <Products
            setCartSum={setCartSum}
            cartSum={cartSum}
            setLoading={setLoading}
            setSortingBy={setSortingBy}
            sortingBy={sortingBy}
            loading={loading}
            t={t}
          />
        </Container>
      </MBox>
    </>
  );
};

export default Filter;
